import React from 'react';
import { Route, Routes } from 'react-router-dom';

const Ssn = React.lazy(() => import('./Ssn/Ssn'));
const LoginCreated = React.lazy(() => import('./LoginCreated/LoginCreated'));

function AuthRouter() {
  return (
    <Routes>
      <Route path="ssn" element={<Ssn />} />
      <Route path="loginCreated" element={<LoginCreated />} />
    </Routes>
  );
}

export default AuthRouter;
